@font-face {
  font-family: "Futura PT";
  src: url("fonts/FuturaPT-Book.eot");
  src: url("fonts/FuturaPT-Book.eot?#iefix") format("embedded-opentype"), url("fonts/FuturaPT-Book.woff2") format("woff2"), url("fonts/FuturaPT-Book.woff") format("woff"), url("fonts/FuturaPT-Book.ttf") format("truetype"), url("fonts/FuturaPT-Book.svg#FuturaPT-Book") format("svg");
  font-weight: 400;
  font-style: normal;
}
.arrow-d {
  display: block;
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  .arrow-d {
    display: none;
  }
}
.arrow-d .arrow {
  cursor: pointer;
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 2.5rem;
  height: 4.375rem;
  z-index: 3;
  padding: 13% 4.5rem;
  box-sizing: content-box;
  transition: opacity 0.2s;
}
.arrow-d .arrow__inner {
  width: 100%;
  height: 100%;
}
.arrow-d .arrow--prev {
  left: 0;
  padding-left: 1.875rem;
}
.arrow-d .arrow--next {
  right: 0;
  padding-right: 1.875rem;
}
.arrow-d .arrow.locked {
  opacity: 0.5;
  cursor: default;
}

.arrow-down,
.arrow-down-popup {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: poiner;
  z-index: 10;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.6;
}
.arrow-down .icon,
.arrow-down-popup .icon {
  width: 7.5rem;
  height: 7.5rem;
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  .arrow-down .icon,
  .arrow-down-popup .icon {
    width: 6.25rem;
    height: 6.25rem;
  }
}
.arrow-down .text-scroll,
.arrow-down-popup .text-scroll {
  color: #ffffff;
  font-size: 16px;
}
.arrow-down img,
.arrow-down-popup img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*  ==================================================
    button

     - basic html -
    <button class="btn" type="button" lcz="text-button"></button>

    - common usage -
    $(".btn").pulse();
    $(".btn").pulseTwices();
    $(".btn").on("click", function() {
      console.log("click"); 
    });

================================================== */
button {
  font-family: "Futura PT";
  outline: none;
  appearance: none;
  background: transparent;
  border: none;
  padding: 0;
  padding-block: 0;
  padding-inline: 0;
}

.btn {
  width: 100%;
  max-width: 33.75rem;
  background-color: #dfdfdf;
  margin: 0 auto;
  cursor: pointer;
  position: relative;
  min-height: 4.875rem;
  padding: 0.3125rem 1.5625rem;
  background-color: #ffffff;
  font-size: 1.875rem;
  font-weight: 700;
  text-transform: uppercase;
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  .btn {
    min-height: 8.625rem;
    max-width: 52.5rem;
    padding: 0.3125rem 1.5625rem;
    font-size: 3rem;
  }
}
.btn:hover .btn__inner {
  opacity: 0.85;
}

.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 99;
}

.lds-roller {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 80px;
  height: 80px;
  z-index: 100;
  opacity: 1;
}

.lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}

.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #000;
  margin: -4px 0 0 -4px;
}

.lds-roller div:nth-child(1) {
  animation-delay: -0.036s;
}

.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.lds-roller div:nth-child(2) {
  animation-delay: -0.072s;
}

.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.lds-roller div:nth-child(3) {
  animation-delay: -0.108s;
}

.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.lds-roller div:nth-child(4) {
  animation-delay: -0.144s;
}

.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.lds-roller div:nth-child(5) {
  animation-delay: -0.18s;
}

.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.lds-roller div:nth-child(6) {
  animation-delay: -0.216s;
}

.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.lds-roller div:nth-child(7) {
  animation-delay: -0.252s;
}

.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.lds-roller div:nth-child(8) {
  animation-delay: -0.288s;
}

.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}

@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*  ==================================================
    scrollbar
================================================== */
/* main scrollbar */
::-webkit-scrollbar {
  width: 4px;
}

/* scroll */
::-webkit-scrollbar-thumb {
  background: #000000;
}

/* track */
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.5);
}

/* hover */
::-webkit-scrollbar-thumb:hover {
  background: #000000;
}

.scroll-inner,
.scroll-popup {
  width: 100%;
  height: 100%;
  overflow-y: overlay;
  overflow-x: hidden;
}

html,
body {
  margin: 0;
  padding: 0;
}

#tangled {
  overflow: hidden;
}
#tangled .bg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 110vw;
  height: 110vh;
  background-image: url("../images/general/bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
#tangled .castle {
  position: absolute;
  width: 58.625rem;
  height: 40.8125rem;
  left: calc(50% - 29.3125rem);
  bottom: 11.25rem;
  filter: blur(3px);
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  #tangled .castle {
    bottom: 28%;
  }
}
#tangled .hero {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: visible;
}
#tangled {
  /* ตัว clip จริง */
}
#tangled .scene-clip {
  position: absolute;
  inset: 0;
  overflow: hidden;
}
#tangled {
  /* ตัวที่ ScrollTrigger pin */
}
#tangled .scene-wrap {
  position: absolute;
  inset: 0;
}
#tangled {
  /* buffer กัน scale */
}
#tangled .scene-buffer {
  width: 120vw;
  height: 120vh;
  transform: translate(-10vw, -10vh);
}
@media (max-width: 768px) {
  #tangled .scene-buffer {
    width: 100vw;
    height: 100vh;
    transform: none;
  }
}
#tangled .scene-wrap::after {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
  background: radial-gradient(circle, transparent 55%, rgba(0, 0, 0, 0.35) 100%);
}
#tangled .lantern-scene {
  width: 100%;
  height: 100%;
  display: block;
}
#tangled .lantern-layer {
  transform-box: fill-box;
  transform-origin: center;
  will-change: transform;
}
#tangled .glow {
  filter: blur(18px);
  opacity: 0.85;
}
#tangled .dust circle {
  fill: #fff6cf;
  opacity: 0.6;
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  #tangled .lantern-sky {
    transform: translate(0%, -30%) !important;
  }
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  #tangled .water-anchor {
    scale: 1 !important;
    transform: translate(0%, -18%) !important;
  }
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  #tangled .water-anchor .water-scale {
    scale: 2.5 !important;
    transform: translate(-16%, -20%) !important;
  }
}
@media screen and (max-width: 768px) and (orientation: portrait) {
  #tangled .water-anchor .boat {
    scale: 1.2;
    transform: translate(58%, 62%);
  }
}
#tangled .valentine-card {
  position: absolute;
  top: 40%;
  left: 50%;
  font-size: 16px;
  transform: translate(-50%, 40px) scale(0.8);
  opacity: 0;
  width: 200px;
  background: rgba(255, 255, 255, 0.8);
  padding: 40px;
  border-radius: 20px;
  text-align: center;
  will-change: transform, opacity;
}
#tangled .lantern-layer.far {
  filter: blur(3px);
}
#tangled .lantern-layer.mid {
  filter: blur(1.5px);
}
#tangled .lantern-layer.near {
  filter: blur(0px);
}